<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right py-3">
      <button
        class="btn btn-large btn-secondary"
        data-toggle="modal"
        data-target="#productModal"
        @click="openModal(true)"
      ></button>
    </div>
    <table class="table">
      <thead class="thead-light">
        <tr>
          <th scope="col" width="80"></th>
          <th scope="col"></th>
          <th scope="col" width="110"></th>
          <th scope="col" width="110"></th>
          <th scope="col" width="100"></th>
          <th scope="col" width="130"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">{{item.origin_price|currency}}</td>
          <td class="text-right">{{item.price|currency}}</td>
          <td>
            <span class="text-success" v-if="item.is_enabled"></span>
            <span class="text-danger" v-if="!item.is_enabled"></span>
          </td>
          <td>
            <button class="btn btn-primary btn-sm" @click="openModal(false,item)"></button>
            <button class="btn btn-danger btn-sm" @click="openModal(false,item,true)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination :pagination="pagination" @gopage="getProductsData"></pagination>
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="isNew"></span>
              <span v-if="!isNew"></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder=""
                  />
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i class="fas fa-spinner fa-spin" v-if="upLoading"></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    @change="upload"
                    class="form-control"
                    ref="files"
                  />
                </div>
                <img :src="tempProduct.imageUrl" class="img-fluid" alt />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder=""
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      v-model="tempProduct.unit"
                      placeholder=""
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      v-model="tempProduct.price"
                      placeholder=""
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="tempProduct.description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      v-model="tempProduct.is_enabled"
                      id="is_enabled"
                    />
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="update"></button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="delProductModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             {{tempProduct.title}} 
            <strong class="text-danger">()</strong>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="delProduct(tempProduct.id)"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../DashboardPagination";
//main.jsbootstrapjQuerypropersbootstrap
// 
export default {
  data() {
    return {
      products: [],
      tempProduct: {
        // title: "",
        // category: "",
        // origin_price: "",
        // price: "",
        // unit: "",
        // image: "",
        // description: "",
        // content: "",
        // is_enabled: 1,
        // imageUrl: ""
      },
      pagination: {},
      isLoading: false,
      isNew: false,
      upLoading: false
    };
  },
  components: {
    Pagination
  },
  methods: {
    getProductsData(page = 1) {
      let vm = this;
      vm.isLoading = true;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      vm.$http.get(api).then(response => {
        console.log("", response);
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
      });
      //propsemitAJAX
      //1
    },
    update() {
      let vm = this;
      vm.isLoading = true;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      let method = "post";
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        method = "put";
      }
      vm.$http[method](api, { data: vm.tempProduct }).then(response => {
        console.log("", response);
        this.getProductsData();
        vm.isLoading = false;
        $("#productModal").modal("hide");
      });
      //isNewapi XHR
    },
    upload() {
      const vm = this;
      // console.log(vm.$refs.files.files[0].name);
      vm.tempProduct.image = vm.$refs.files.files[0].name;
      const uploadedFile = vm.$refs.files.files[0];
      vm.upLoading = true;
      const formData = new FormData();
      formData.append("file-to-upload", uploadedFile);
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      vm.$http
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(response => {
          // console.log(response.data, response, "");
          vm.upLoading = false;
          console.log(response.data, vm.$bus);
          if (response.data.success) {
            // vm.tempProduct.imageUrl = response.data.imageUrl;
            // console.log(vm.tempProduct);
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
            // console.log(vm.tempProduct);
            vm.$bus.$emit("message:push", "", "success");
          } else {
            vm.$bus.$emit("message:push", response.data.message, "danger");
          }
        });
    },
    openModal(isNew, item, isDel) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else if (!isNew && isDel) {
        this.tempProduct = Object.assign({}, item);
        $("#delProductModal").modal("show");
      } else {
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
        $("#productModal").modal("show");
      }
      //modalmodal
      //tempProduct
    },
    delProduct(id) {
      let vm = this;
      vm.isLoading = true;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${id}`;
      vm.$http.delete(api).then(response => {
        console.log("", response);
        this.getProductsData();
        vm.isLoading = false;
        $("#delProductModal").modal("hide");
      });
    }
    // addFileName() {
    //   // console.log($("#customFile").val());
    //   let fileVal = $("#customFile").val();
    //   // console.log(fileVal.lastIndexOf("\\"));
    //   let index = fileVal.lastIndexOf("\\");
    //   // console.log(fileVal.substring(index + 1));
    //   this.tempProduct.image = fileVal.substring(index + 1);
    //   //changelastIndexOf'\\'indexindexsubstring()return
    // }
    //ref
  },
  created() {
    this.getProductsData();
  }
};
</script>



