<template>
  <div class="container pt-md-3 pb-md-5 pgHightRevise">
    <div class="row">
      <div class="col-12">
        <div class="form-row">
          <div class="col-12 col-sm">
            <div class="alert alert-primary text-center alert-rounded" role="alert">1.</div>
          </div>
          <div class="col-12 col-sm">
            <div class="alert alert-light text-center alert-rounded" role="alert">2.</div>
          </div>
          <div class="col-12 col-sm">
            <div class="alert alert-light text-center alert-rounded" role="alert">3.</div>
          </div>
        </div>
      </div>
      <form class="col-md-8 mt-md-3 px-4">
        <div class="form-row mb-3">
          <div class="col-12 h5 mb-3 text-center"></div>
          <div class="col-md-6">
            <label for="username"></label>
            <input
              type="text"
              class="form-control"
              :class="{'is-invalid':errors.has('name')}"
              name="name"
              id="username"
              v-validate="'required'"
              placeholder=""
            />
            <span class="text-danger" v-if="errors.has('name')"></span>
          </div>
          <div class="col-md-6">
            <label for="usertel"></label>
            <input
              type="tel"
              class="form-control"
              :class="{'is-invalid':errors.has('tel')}"
              id="usertel"
              name="tel"
              placeholder=""
              v-validate="'required'"
            />
            <span class="text-danger" v-if="errors.has('tel')"></span>
          </div>
        </div>

        <div class="form-group">
          <label for="useremail">Email</label>
          <input
            type="email"
            class="form-control"
            :class="{'is-invalid':errors.has('email')}"
            name="email"
            id="useremail"
            v-validate="'required|email'"
            placeholder=" Email"
          />
          <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
        </div>

        <div class="form-group">
          <label for="useraddress"></label>
          <input
            type="text"
            class="form-control"
            :class="{'is-invalid':errors.has('address')}"
            name="address"
            id="useraddress"
            v-validate="'required'"
            placeholder=""
          />
          <span class="text-danger" v-if="errors.has('address')"></span>
        </div>

        <div class="form-group">
          <label for="comment">
            
            <small class="text-muted">()</small>
          </label>
          <textarea name id="comment" class="form-control" cols="30" rows="4"></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-light btn-block"></button>
        </div>
      </form>
      <div class="col-md-4 border shadow">
        <div class="h5 text-center mt-3"></div>
        <ul class="list-group list-group-flush">
          <li class="list-group-item">Cras justo odio</li>
          <li class="list-group-item">Dapibus ac facilisis in</li>
          <li class="list-group-item">Morbi leo risus</li>
          <li class="list-group-item">Porta ac consectetur ac</li>
          <li class="list-group-item">Vestibulum at eros</li>
        </ul>
        <a href="#" class="btn btn-outline-success btn-block mt-4"></a>
      </div>
    </div>
  </div>
</template>

<script>
//vee-validateerrorshas,firsthasinputname
export default {
  methods: {}
};
</script>


